@media only screen and (min-width: 900px) and (max-width: 2100px) {
  .billete {
    width: 43%;
    height: 22%;
    cursor: Pointer;
    font-size: 1.8rem;
    font-family: "Lato", sans-serif;
    color: rgb(243, 243, 243);
    background: dimgray;
    outline: none;
    border: none;
    margin: 0 3%;
  }

  .billete:hover {
    width: 43%;
    height: 22%;
    cursor: Pointer;
    font-size: 1.8rem;
    font-family: "Lato", sans-serif;
    color: rgb(243, 243, 243);
    background: #8da47d;
    outline: none;
    border: none;
    margin: 0 3%;
  }
}
