@media only screen and (min-width: 900px) and (max-width: 2100px) {
  .header {
    width: 100%;
    height: 5rem;
    background: #8da47d;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .header-titulo {
    color: rgb(243, 243, 243);
    margin-left: 4%;
    font-family: 'Lato', sans-serif;
    font-size: 2rem;
  }

  .header_contenedor-botones {
    width: 60%;
    max-width: 450px;
    height: 60%;
    margin-right: 4%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .header_boton {
    height: 80%;
    padding: 0 5% 1% 5%;
    outline: none;
    cursor: pointer;
    background: #8da47d;
    border: 2px rgb(243, 243, 243) solid;
    color: rgb(243, 243, 243);
    border-radius: 10px;
    cursor: pointer;
    font-family: 'Lato', sans-serif;
  }

  .header_boton:hover {
    background: rgb(243, 243, 243);
    border: 2px #7c7c7c solid;
    color: #7c7c7c;
  }
}




