.producto {
  box-sizing: border-box;
  width: 29.333%;
  height: 30%;
  margin: 2% 2%;
  background: #ffffff;
  position: relative;
  display: flex;
  justify-content: center;
}

.producto_contenedor-codigo {
  box-sizing: border-box;
  width: 100%;
  height: 40px;
  position: absolute;
  top: 0;
  background: cadetblue;
  padding: 0 5%;
  display: flex;
  align-items: center;
}

.producto_codigo-titulo {
  font-size: 1.2rem;
  font-family: "Lato", sans-serif;
  color: rgb(243, 243, 243);
}

.producto_codigo {
  font-size: 1.2rem;
  font-family: "Lato", sans-serif;
  color: rgb(243, 243, 243);
  font-weight: 700;
  margin-left: 20px;
}

.producto_nombre {
  font-size: 1.2rem;
  font-family: "Lato", sans-serif;
  color: #565656;
  position: absolute;
  top: 20%;
}

.producto__otra-informacion-contenedor {
  width: 65%;
  height: 35px;
  position: absolute;
  top: 50%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.producto__otra-informacion {
  font-size: 1.2rem;
  font-family: "Lato", sans-serif;
  color: #999999;
  font-weight: 700;
}

.producto__contenedor-botones {
  width: 90%;
  height: 35px;
  position: absolute;
  top: 75%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.producto__boton-eliminar {
  width: 40%;
  height: 100%;
  border: none;
  outline: none;
  cursor: pointer;
  background: #cecece;
  font-size: 1.2rem;
  font-family: "Lato", sans-serif;
  color: #565656;
}

.producto__boton-eliminar:hover {
  width: 40%;
  height: 100%;
  border: none;
  outline: none;
  cursor: pointer;
  background: rgb(151, 197, 151);
  font-size: 1.2rem;
  font-family: "Lato", sans-serif;
  color: #565656;
}

.producto__boton-editar {
    width: 40%;
    height: 100%;
    border: none;
    outline: none;
    cursor: pointer;
    background: #cecece;
    font-size: 1.2rem;
    font-family: "Lato", sans-serif;
    color: #565656;
  }
  
  .producto__boton-editar:hover {
    width: 40%;
    height: 100%;
    border: none;
    outline: none;
    cursor: pointer;
    background: rgb(151, 197, 151);
    font-size: 1.2rem;
    font-family: "Lato", sans-serif;
    color: #565656;
  }